.hero {
  // .main__info

  min-height: 100vh;
  @include tablet {
    height: auto;
  }

  &__content {
    position: relative;
    transition: all 1s ease;
    /*   display: flex; */
    display: grid;
    grid-template-columns: 597px 1fr;
    min-height: 100vh;
    &.project-visible {
      grid-template-columns: 0px 1fr;
      .swiper-nav {
        button.home-back {
          animation: btnVisible 1s forwards;
        }
      }
      .hero__project-upper {
        display: flex;
      }
      .tabs__nav {
        transform: translateX(140px);
      }
      .hero__tabs-upper {
      }
      .hero__info {
        transform: translateX(-100%);
        opacity: 0;
      }
      .home-back.visible {
        opacity: 1;
        pointer-events: all;
      }
      .hero__info-container {
        min-width: 436px;
      }
      .logo {
        visibility: hidden;
      }
      .logo.visible {
        display: block;
      }
      .logo.project {
        display: block;
        visibility: visible;
        opacity: 1;
      }
      .info-graph-swiper {
        display: none;
      }
      .hero__tabs {
        width: 100%;
      }

      .footer {
        animation: footerOut 0.5s forwards;
      }
      .info-graph-projects-swiper {
        visibility: visible;
        position: relative;
        left: 0;
      }
      .logo.project.mobile {
        display: block;
      }
    }
    @include tablet {
      height: auto;
      display: grid;
      grid-template-columns: 1fr;
    }
    .hero__info-container:not(.mobile),
    .hero__model-container:not(.mobile) {
      position: absolute;
      top: 50%;
      display: flex;
      width: 100%;
      transform: translateY(-50%);
      transition: none;
    }

    &:has(.info-graph-button.animate) {
      @media (min-width: 1025px) {
        .hero__info-container:not(.mobile) {
          animation: titleTo 1s ease-in forwards;
          pointer-events: all;
        }
        .hero__model-container:not(.mobile) {
          animation: titleOut 0.4s ease-in forwards;
          pointer-events: none;
        }
      }
    }
    &:has(.model-graph-button.animate) {
      @media (min-width: 1025px) {
        .hero__info-container:not(.mobile) {
          animation: titleOut 0.4s ease-in forwards;
        }
        .hero__model-container:not(.mobile) {
          pointer-events: none;
          animation: titleTo 1s ease-in forwards;
          pointer-events: all;
        }
      }
    }
  }
  &__info-middle {
    display: flex;
    flex-direction: column;
    flex: 1;
    position: relative;
  }
  &__info {
    transition: all 1s ease;
    width: 597px;
  }
  &__project-upper {
    position: absolute;
    top: 30px;
    left: 80px;
    z-index: 5;
    height: fit-content;
    align-items: center;
    button {
      font-family: var(--font-family);
      font-weight: 400;
      font-size: 16px;

      color: #737373;
    }
    button.active {
      font-family: var(--font-family);
      font-weight: 400;
      font-size: 16px;

      color: #fff;
    }
    .logo {
      display: block;
      position: static;
      margin-right: 536px;
    }
  }
  &__project-upper-btns {
    display: flex;
    align-items: center;
    gap: 40px;
    flex: 1;
  }
  &__info {
    border-right: 1px solid #303030;
    height: 100%;
    display: flex;
    flex-direction: column;
    flex: 0 0 auto;
    opacity: 1;
    min-height: 100vh;
  }
  &__info-container {
    transition: 1s ease-in;
    display: flex;
    flex-direction: column;
    gap: clamp(2.188rem, 1.25rem + 1.04vw, 2.5rem);
    flex: 1;

    max-width: clamp(24.938rem, 17.063rem + 8.75vw, 27.263rem);
    justify-content: center;
    @include big-desktop {
      gap: 35px;
    }
    @include tablet {
      display: none;
      padding: 0;
    }
    &.mobile {
      display: none;
      @include tablet {
        display: flex;
      }
    }
    .hero__title {
      margin-top: -5px;
      @include big-desktop {
        line-height: 66px;
      }
    }
    @include tablet {
      max-width: 596px;
      margin: 0 auto;
    }
    @include mobile {
      gap: 25px;
    }
  }
  &__model-container {
    display: flex;
    flex-direction: column;
    gap: clamp(2.188rem, 1.25rem + 1.04vw, 2.5rem);
    flex: 1;
    transition: 1s ease-in;
    display: none;
    justify-content: center;
    max-width: clamp(24.938rem, 17.063rem + 8.75vw, 27.263rem);
    opacity: 0;
    pointer-events: none;
    .swiper-nav {
      padding: 0;
    }
    @include tablet {
      display: none;
      padding: 0;
    }
    &.mobile {
      display: none;
      @include tablet {
        display: flex;
      }
    }
    .hero__title {
      margin-top: -5px;
      span {
        margin-left: 5px;
        display: block;
      }
    }
    @include tablet {
      max-width: 596px;
      margin: 0 auto;
    }
  }
  // .main__title

  &__title {
    font-family: var(--font-family);
    font-weight: 600;
    font-size: clamp(4.125rem, 2.25rem + 2.08vw, 4.75rem);
    line-height: 76px;
    color: #fff;
    margin: 0;
    margin-left: -5px;

    @include big-desktop {
      font-size: clamp(3.125rem, 0.663rem + 3.85vw, 4.125rem);
    }
    @include tablet {
      font-weight: 600;
      font-size: 104px;
      margin-left: -10px;
    }
    @include mobile {
      margin-left: -5px;
      font-size: 50px;
    }
  }

  // .main__desc

  &__desc {
    font-family: var(--font-family);
    font-weight: 400;
    font-size: clamp(1rem, 0.625rem + 0.42vw, 1.125rem);
    line-height: 28px;
    color: #737373;
    margin: 0;
    @include big-desktop {
      font-size: clamp(0.938rem, 0.784rem + 0.24vw, 1rem);
      line-height: 26px;
    }
    @include tablet {
      font-size: 18px;
    }
    @include mobile {
      font-size: 15px;
    }
  }

  // .main__link

  &__link {
    font-family: var(--font-family);
    font-weight: 600;
    font-size: clamp(1rem, 0.625rem + 0.42vw, 1.125rem);
    text-align: center;
    color: #fff;
    display: flex;
    align-items: center;
    gap: 20px;
    width: fit-content;
    @include big-desktop {
      font-size: 16px;
     
    }
    @include tablet {
      font-size: 18px;
    }
  }
  &__tabs-upper {
    width: 100%;
    padding-top: 42px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    transition: all 1s ease;
    @include big-desktop {
      padding-top: 35px;
    }
    @include tablet {
      margin-bottom: 100px;
    }
    @include mobile {
      margin-bottom: 81px;
      .hero__tabs-upper-links {
        display: none;
      }
    }

    &.mobile {
      display: none;
      @include tablet {
        display: flex;
      }
    }
  }
  &__tabs-upper-links {
    display: flex;
    gap: 25px;
    align-items: center;
    @include tablet {
      img {
        min-width: 20px;
      }
    }
    @include mobile {
      gap: 20px;
      img {
        min-width: 16px;
      }
    }
  }
  &__tabs {
    min-width: 0;
  }
  .tabs__content {
    display: flex;
    .tabs__panel {
      width: 100%;
      top: 0;
      display: flex !important;
      opacity: 0;
      transition: none;
      transform: translateX(100%);

      &.second {
        transform: translateX(0);
      }
      &--active {
        display: flex !important;
        opacity: 1;
        top: 0;
        transform: translateX(0);
      }
      &.second.tabs__panel--active {
        transform: translateX(-100%);
      }
    }
  }
}
.loaded .hero .tabs__content .tabs__panel {
  transition: transform 1s ease-in, opacity 0.4s ease-in;
}
.hero__info-container:not(.mobile),
.hero__model-container:not(.mobile) {
  animation: none;
}
.tabs__nav {
  transition: all 1s ease;
}

.info-graph-more {
  margin-right: 30px;
  margin-left: 30px;
  padding-bottom: 20px;
  border-bottom: 1px solid #404040;
  margin-top: -50px;
  display: none;
  @include tablet {
    display: flex;
    margin-right: unset;
    margin-left: unset;
    margin-top: -40px;
  }
  @include mobile {
    margin-top: -30px;
    font-size: 15px;
    gap: 30px;
  }
  align-items: center;
  justify-content: space-between;
  font-family: var(--font-family);
  font-weight: 400;
  font-size: 18px;
  line-height: 128%;
  color: #fff;
}
