.footer {
  // .footer__copy
  border-top: 1px solid #303030;
  padding: 30px 59px 30px 80px;

  margin: 0 -80px;
  margin-top: -30px;
  padding-top: 30px;
  @include tablet {
    display: none;
  }
  &.mobile {
    margin-top: 60px;
    display: none;
    padding: 30px 0 150px 0;
    margin: 100px 30px 0 30px;
    @include tablet {
      display: block;
    }
    @include mobile {
      padding: 20px 0 20px 0;
      margin: 60px 20px 0 20px;
    }
  }
  &__content {
    text-wrap: nowrap;
    max-width: clamp(24.75rem, 11.813rem + 14.37vw, 29.063rem);
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: clamp(1.563rem, 0.625rem + 1.04vw, 1.875rem);
    @include big-desktop{
      gap: 25px;
    }
    @include tablet {
      justify-content: space-between;
      min-width: 100%;
    }
    @include mobile {
      gap: 70px;
      align-items: flex-start;
      justify-content: unset;
    }
  }
  &__copy {
    font-family: var(--font-family);
    font-weight: 400;
    font-size: clamp(0.75rem, 0.375rem + 0.42vw, 0.875rem);
    line-height: 171%;
    color: #737373;
    @include big-desktop {
      font-size: 12px;
      line-height: 24px;
    }
    @include tablet {
      font-size: 14px;
    }
    @include mobile {
      font-size: 12px;
      color: rgba(255, 255, 255, 0.25);
    }
  }

  // .footer__link
  &__links {
    display: flex;
    gap: 8px;
    align-items: center;
    @include tablet {
      gap: 30px;
    }
    @include mobile {
      flex-direction: column;
      align-items: flex-start;
      gap: 8px;
    }
  }
  &__link {
    font-family: var(--font-family);
    font-weight: 400;
    font-size: clamp(0.75rem, 0.375rem + 0.42vw, 0.875rem);
    line-height: 171%;
    color: #737373;
    transition: 0.3s ease-in;
    @include hover {
      color: #fff;
    }
    @include tablet {
      font-size: 14px;
    }
    @include mobile {
      font-size: 12px;
      color: rgba(255, 255, 255, 0.25);
    }
  }
}
